import styled from 'styled-components'

export default styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 400px;
  z-index: 1;
  background: white;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.15), 0px 0px 8px rgba(0, 0, 0, 0.05);
  padding: 20px;
`



