import styled from 'styled-components'

export default styled.main`
  display: grid;
  grid-template-areas: 'nav content';
  grid-template-rows: auto;
  grid-template-columns: 250px auto;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
`



